<template>
  <div>
    <div>
      <van-nav-bar title="黑马程序员" left-text="返回" left-arrow @click-left="$router.push(`/Search`)" />
    </div>
    <div>
      <van-cell-group>
        <van-field v-model="value" placeholder="请输入商品名称" />
      </van-cell-group>
    </div>
    <van-button @click="addGoods">添加商品</van-button>
    <g-tabbar />
  </div>
</template>

<script>

import GTabbar from '../tabbar'
export default {
  data () {
    return {
      value: ''
    }
  },
  methods: {
    async addGoods () {
      const data = await this.$http.post('/api/addproduct', { name: this.value })
      data.status !== 0
        ? this.$notify({ type: 'danger', message: '添加失败' })
        : this.$notify({ type: 'success', message: '添加成功' })
      this.$router.push('/Search')
    }
  },
  created () {
  },
  components: {

    GTabbar
  }
}
</script>
<style lang="less" scoped>
.van-button {
  height: 40px;
  background: #1989fa;
  border: 1px solid #1989fa;
  line-height: 38px;
  margin: 40px 10px;
  width: 100%;
  font-size: 16px;
  color: #fff;
}
.van-cell-group {
  margin: 50px 10px 0 10px;
}
</style>
